      <vuestic-modal-bakterije
        :show.sync="show"
        :sample="sample"
        :large="sveBakterije"
        :neg="largeModalneg"
        :pos="largeModalpos"
        ref="largeModalBakterije"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          <span style="color: #acb5be;">{{Header}} |</span>
          <span style="color: #e34a4a;">{{"Izbornik"}}</span>
        </div>
        <div class="row">

          <div class="col-md-1" v-if="MikrobioloskiUzorak && largeModalneg"></div>
          <div class="col-md-11" v-if="MikrobioloskiUzorak && largeModalneg">
            <vuestic-radio-button
              v-for="(desc, index) in RezultatMikrobioloski.neg_descriptions"
              :key="index"
              :label="RezultatMikrobioloski.neg_descriptions[index]"
              :id="RezultatMikrobioloski.neg_descriptions[index]"
              :value="RezultatMikrobioloski.neg_descriptions[index]"
              :name="RezultatMikrobioloski.neg_descriptions[index]"
              :disabled="false"
              v-model="izborNegativnogRezultata"
            />
          </div>

          <div class="col-md-1" v-if="largeModalpos && !RezultatMikrobioloski.Bakterije.length"></div>              
          <div class="col-md-11" v-if="largeModalpos && !RezultatMikrobioloski.Bakterije.length">
            <vuestic-radio-button
              v-for="(desc, index) in RezultatMikrobioloski.pos_descriptions"
              :key="index"
              :label="RezultatMikrobioloski.pos_descriptions[index]"
              :id="RezultatMikrobioloski.pos_descriptions[index]"
              :value="RezultatMikrobioloski.pos_descriptions[index]"
              :name="RezultatMikrobioloski.pos_descriptions[index]"
              :disabled="false"
              v-model="izborPozitivnogRezultata"
            />
          </div>  

          <div class="col-md-6" v-if="!largeModalneg && !largeModalpos">
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-6"> 
                <button 
                  style="text-transform: none; border: 0px;" 
                  class="btn btn-secondary btn-sm wizard-back pull-left" 
                  @click="largeModalneg = true; largeModalpos = false;">
                  {{'Negativan'}}</button>              
                </div>
              <div class="col-md-3"></div>
            </div>
          </div>              
          <div class="col-md-6" v-if="!largeModalneg && !largeModalpos">
            <div class="row">
              <div class="col-md-2"></div>
              <div class="col-md-6">                
                <button 
                  style="text-transform: none; border: 0px;" 
                  class="btn btn-secondary-danger btn-sm wizard-next pull-right" 
                  @click="largeModalpos = true; largeModalneg = false;">
                  {{'Pozitivan'}}</button>
              </div>
              <div class="col-md-4"></div>
            </div>
          </div>              

          <!--  -->

          <div class="col-md-6" v-if="MikrobioloskiUzorak">
            <div class="row" v-if="largeModalpos && RezultatMikrobioloski.Bakterije.length">
              <div class="col-md-2"></div>
              <div class="col-md-10">
                <div
                  class="form-check abc-checkbox abc-checkbox-primary"
                  v-for="(bakt, index) in RezultatMikrobioloski.Bakterije"
                  :key="index"
                >
                  <input
                    class="form-check-input"
                    :id="bakt._id"
                    type="checkbox"
                    v-model="bakt.chosen"
                    @click="SelectBactery(bakt, index)"
                  />
                  <label class="form-check-label" for="checkbox">
                    <span style="color: #e34a4a;" class="abc-label-text">{{bakt.opis}}</span>
                  </label>
                </div>
              </div>
            </div>  

          </div>

          <!--  -->

          <div class="col-md-6">

            <div class="row" v-if="!largeModalneg && !largeModalpos ">
              
            </div>

            <div class="row" v-if="largeModalpos && RezultatMikrobioloski.Bakterije.length">
              <div class="col-md-1"></div>
              <div class="col-md-6">
                <a
                  v-if="!sveBakterije"
                  href="#"
                  @click.prevent="sveBakterije = true"
                  style="font-size: 16px; color: #4ae387;"
                >Prikaži više</a>
              </div>
              <div class="col-md-5"></div>
            </div>

            <div class="row" v-if="sveBakterije">
              <div class="col-md-2"></div>
              <div class="col-md-10">
                <div
                  class="form-check abc-checkbox abc-checkbox-primary"
                  v-for="(bakt, index) in RezultatMikrobioloski.OstaleBakterije"
                  :key="index"
                >
                  <input
                    class="form-check-input"
                    :id="bakt._id"
                    type="checkbox"
                    v-model="bakt.chosen"
                    @click="SelectBactery(bakt, index)"
                  />
                  <label class="form-check-label" for="checkbox">
                    <span style="color: #e34a4a;" class="abc-label-text">{{bakt.opis}}</span>
                  </label>
                </div>
              </div>
            </div>
          </div>

        </div>
      </vuestic-modal-bakterije>


      <vuestic-modal-bakterije
        :show.sync="show"
        :sample="sample"
        :large="false"
        :neg="largeModalneg"
        :pos="largeModalpos"
        :antibiotici="true"
        ref="largeModalAntibiotici"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          <span style="color: #acb5be;">Antibiotici |</span>
          <span style="color: #e34a4a;">{{"Izbornik"}}</span>
        </div>
        <div class="row">

          <!--  -->

          <div class="col-md-12" v-if="antibiotici.length">
            <div class="row">
              <div class="col-md-1"></div>
              <div class="col-md-5">               
                 <div
                  class="form-check abc-checkbox abc-checkbox-primary"
                  v-for="antibiotik in filteredAntibiotici(1, 2)"
                  :key="antibiotik._id"
                >
                  <input
                    class="form-check-input"
                    :id="antibiotik._id"                    
                    type="checkbox"
                    v-model="antibiotik.chosen"
                    @click="addAntibiotik(antibiotik)"
                    :disabled="antibiotik.disabled"
                  />
                  <label class="form-check-label" for="checkbox">
                    <span v-if="!antibiotik.chosen" style="color: #e34a4a;" class="abc-label-text">{{antibiotik.opis}}</span>
                    <span v-if="antibiotik.chosen" style="color: #4ae387;" class="abc-label-text">{{antibiotik.opis}}</span>
                  </label>
                </div>
              </div>
              <div class="col-md-6">               
                <div
                  class="form-check abc-checkbox abc-checkbox-primary"
                   v-for="antibiotik in filteredAntibiotici(2, 2)"
                  :key="antibiotik._id"
                >
                  <input
                    class="form-check-input"
                    :id="antibiotik._id"
                    type="checkbox"
                    v-model="antibiotik.chosen"
                    @click="addAntibiotik(antibiotik)"
                    :disabled="antibiotik.disabled"
                  />
                  <label class="form-check-label" for="checkbox">
                    <span v-if="!antibiotik.chosen" style="color: #e34a4a;" class="abc-label-text">{{antibiotik.opis}}</span>
                    <span v-if="antibiotik.chosen" style="color: #4ae387;" class="abc-label-text">{{antibiotik.opis}}</span>
                  </label>
                </div>
              </div>
            </div>  

          </div>
         
        </div>
      </vuestic-modal-bakterije>

      <!-- Reset mikrobiološkog uzorka -->
      <vuestic-modal-delete-result
        :show.sync="show"
        ref="staticModalDeleteResult"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          <span style="color: #acb5be;">{{'Brisanje rezultata: '}}</span>
          <span style="color: #e34a4a;">{{uzorak_micro}}</span>
        </div>
        <div>
          <h5>{{ 'Jeste li sigurni da želite obrisati rezultat?'}}</h5>
        </div>
      </vuestic-modal-delete-result>
