      <!-- Brisanje uzorka -->
      <vuestic-modal-delete
        :show.sync="show"
        ref="staticModalDeleteSample"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">{{ 'Brisanje uzorka: ' + sid }}</div>
        <div>
          <h5>{{ 'Jeste li sigurni da želite obrisati uzorak?'}}</h5>
        </div>
      </vuestic-modal-delete>
      <!-- Unos komentara -->
      <vuestic-modal-comment
        :show.sync="show"
        ref="staticModalComment"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">{{ 'Unos komentara za uzorak: ' + sid }}</div>
        <div>
          <h5></h5>
        </div>
      </vuestic-modal-comment>
      <!-- Uređivanje uzorka -->
      <vuestic-modal-edit
        :show.sync="show"
        :sample="sid"
        action="Edit"
        ref="staticModalEditSample"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">{{ 'Uređivanje uzorka: ' + sid }}</div>
        <div>
          <h5></h5>
        </div>
      </vuestic-modal-edit>
      <!-- Email Send -->
      <vuestic-modal-mail
        :show.sync="show"
        ref="staticModalSendMail"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{'Slanje nalaza na adresu: '}}
          <span style="color: #4ae387;">{{email}}</span>
        </div>
        <div>
          <h5>{{'Jeste li sigurni da želite poslati nalaz?'}}</h5>
        </div>
      </vuestic-modal-mail>

      <!--  -->
      <vuestic-modal-all-results
        :show.sync="show"
        ref="staticModalAllResults"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{'Izbor rezultata: '}}
          <span style="color: #e34a4a;">{{labassay}}</span>
        </div>
        <div>
          <div class="table-responsive">
            <table class="table table-striped first-td-padding">
              <thead>
                <tr>
                  <!-- <td width="20%"></td> -->
                  <td>analizator</td>
                  <td>vrijeme</td>
                  <td>rezultat</td>
                  <td>jedinica</td>
                  <td>reference</td>
                  <td v-if="sample_status != 'ODOBREN' &&  sample_status != 'VERIFICIRAN'">izbor</td>
                  <td align="middle"></td>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="test in vise.rezultat"
                  v-show="test.rezultat_f != ''"
                  :key="test._id"
                  v-bind:class="{
                    'table-danger': Interpretacija(vise.labassay.analit, test.rezultat_f, test.jedinice_f, vise.refd, vise.refg, $store.state.site, vise.interp, vise.extend, spec)[5] === 'Red' && !test.multidata, 
                    'table-success': Interpretacija(vise.labassay.analit, test.rezultat_f, test.jedinice_f, vise.refd, vise.refg, $store.state.site, vise.interp, vise.extend, spec)[5] === 'Green' && !test.multidata,
                    'table-warning': Interpretacija(vise.labassay.analit, test.rezultat_f, test.jedinice_f, vise.refd, vise.refg, $store.state.site, vise.interp, vise.extend, spec)[5] === 'Yellow' && !test.multidata
                  }"
                >
                  <td>{{ test.anaassay.aparat.ime }}</td>

                  <td>
                    {{ JSON.stringify(
                    test.updated_at
                    ).slice(9, 11) +
                    "." +
                    JSON.stringify(
                    test.updated_at
                    ).slice(6, 8) +
                    "." +
                    JSON.stringify(
                    test.updated_at
                    ).slice(1, 5) + ' | '}}
                    <sub>
                      {{JSON.stringify(
                      test.updated_at
                      ).slice(12, 20)}}
                    </sub>
                  </td>
                  <td>{{ test.rezultat_f }}</td>
                  <td>{{ test.jedinice_f }}</td>
                  <td>{{Interpretacija(vise.labassay.analit, test.rezultat_f, test.jedinice_f, vise.refd, vise.refg, $store.state.site, vise.interp, vise.extend, spec)[4]}}</td>
                  <td v-if="sample_status != 'ODOBREN' &&  sample_status != 'VERIFICIRAN'">
                    <i
                      v-if="!test.izabran"
                      @click.prevent="IzborTesta(vise, test)"
                      style="font-size: 18px; color:#e34a4a;"
                      class="glyphicon glyphicon-unchecked"
                    ></i>

                    <i
                      v-if="test.izabran"
                      style="font-size: 18px; color:#4ae387;"
                      class="glyphicon glyphicon-check"
                    ></i>
                  </td>
                  <td>
                    <i
                      v-bind:class="{'fa fa-exclamation-triangle icon-right input-icon error-icon' : Interpretacija(vise.labassay.analit, test.rezultat_f, test.jedinice_f, vise.refd, vise.refg, $store.state.site, vise.interp, vise.extend, spec)[5] === 'Red'}"
                    ></i>
                    <i
                      v-bind:class="{'fa fa-check success-icon icon-right input-icon' : Interpretacija(vise.labassay.analit, test.rezultat_f, test.jedinice_f, vise.refd, vise.refg, $store.state.site, vise.interp, vise.extend, spec)[5] === 'Green'}"
                    ></i>
                  </td>
                </tr>
              </tbody>
            </table>
            <!-- END OF DRUGA TABELA -->
          </div>
        </div>
      </vuestic-modal-all-results>

      <vuestic-modal-error
        :show.sync="show"
        ref="staticModalError"
        cancelText="ZATVORI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{''}}
          <span style="color: #e34a4a;">{{'404: Not Found'}}</span>
        </div>
        <div>
          <h5>{{'Greška prilikom čitanja nalaza.'}}</h5>
        </div>
      </vuestic-modal-error>

      <vuestic-modal-error
        :show.sync="show"
        ref="staticModalVerifyPrivilege"
        cancelText="ZATVORI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{''}}
          <span style="color: #e34a4a;">{{'401: Not Authorized'}}</span>
        </div>
        <div>
          <h5>{{'Nemate odgovarajuće privilegije.'}}</h5>
        </div>
      </vuestic-modal-error>

      <vuestic-modal-exclamation
        :show.sync="show"
        ref="staticModalExclamation"
        cancelText="ZATVORI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{''}}
          <span style="color: #e34a4a;">{{warning.analiza}}</span>
        </div>
        <div>
          <h5>{{warning.control}}</h5>
          <p>{{warning.text}}</p>
        </div>
        <!-- <div v-if="warning.napomena !=''">
          <h5>{{"Napomena"}}</h5>
          <p>{{warning.napomena}}</p>
        </div> -->
      </vuestic-modal-exclamation>

      <vuestic-modal-reference
        :show.sync="show"
        ref="staticModalReference"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{'Referentne vrijednosti: '}}
          <span
            style="color: #e34a4a;"
          >{{reference.naziv}} ({{reference.opis}})</span>
        </div>
        <div>
          <div class="row">
            <div class="col-md-1"></div>
            <div class="col-md-2">
              <div class="form-group">
                <div class="input-group">
                  <input
                    style="color: #e34a4a;"
                    id="input-icon-left"
                    title=" "
                    v-model="reference.dref"
                    name="input-icon-left"
                    required
                  />
                  <!-- <i class="glyphicon glyphicon-pencil icon-left input-icon"></i> -->
                  <label class="control-label" for="input-icon-left">{{'Donja'}}</label>
                  <i class="bar"></i>
                </div>
              </div>
            </div>
            <div class="col-md-1"></div>
            <div class="col-md-2">
              <div class="form-group">
                <div class="input-group">
                  <input
                    style="color: #e34a4a;"
                    id="input-icon-left"
                    title=" "
                    v-model="reference.gref"
                    name="input-icon-left"
                    required
                  />
                  <!-- <i class="glyphicon glyphicon-pencil icon-left input-icon"></i> -->
                  <label class="control-label" for="input-icon-left">{{'Gornja'}}</label>
                  <i class="bar"></i>
                </div>
              </div>
            </div>
            <div class="col-md-1"></div>
            <div class="col-md-4">
              <vuestic-simple-select
                v-if="$store.state.access.level < 1"
                style="font-size: 14px;"
                label="Interpretacija"
                v-model="reference.interp"
                title=" "
                v-bind:options="interpretacije"
              ></vuestic-simple-select>

              <div v-if="$store.state.access.level > 0" class="form-group">
                <div class="input-group">
                  <input
                    id="input-icon-left"
                    title=" "
                    v-model="reference.interp"
                    name="input-icon-left"
                    required
                    :disabled="true"
                  />
                  <!-- <i class="glyphicon glyphicon-pencil icon-left input-icon"></i> -->
                  <label class="control-label" for="input-icon-left">{{reference.interp}}</label>
                  <i class="bar"></i>
                </div>
              </div>
            </div>
            <div class="col-md-1"></div>
          </div>

          <div class="row">
            <div class="col-md-1"></div>
            <div class="col-md-2">
              <vuestic-simple-select
                style="font-size: 14px;"
                label="Jedinica"
                v-model="reference.jedinica"
                title=" "
                v-bind:options="jedinice"
              ></vuestic-simple-select>
            </div>
            <div class="col-md-1"></div>

            <div class="col-md-7">
              <div class="form-group" v-show="reference.interp != 'spec'">
                <div class="input-group">
                  <input
                    style="color: #e34a4a;"
                    id="input-icon-left"
                    title=" "
                    v-model="reference.extend"
                    name="input-icon-left"
                    required
                  />

                  <label
                    class="control-label"
                    for="input-icon-left"
                  >{{'Proširena referentna vrijednost'}}</label>
                  <i class="bar"></i>
                </div>
              </div>

              <div class="form-group" v-show="reference.interp === 'spec'">
                <div class="input-group">
                  <textarea
                    v-model="reference.extend"
                    type="text"
                    style="color: #e34a4a;"
                    id="simple-textarea"
                    required
                  ></textarea>

                  <label
                    class="control-label"
                    for="simple-textarea"
                  >{{'Specifična interpretacija rezultata'}}</label>
                  <i class="bar"></i>
                </div>
              </div>
            </div>
            <div class="col-md-1"></div>
          </div>
        </div>
      </vuestic-modal-reference>

      <vuestic-modal-find
        :show.sync="show"
        :data_pid="Number(data_pid)"
        :data_length="Number(data_length)"
        ref="staticModalFind"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{''}}
          <span style="color: #e34a4a;">{{'Redni broj pacijenta'}}</span>
        </div>
        <div>
          <h5></h5>
        </div>
      </vuestic-modal-find>

      <vuestic-modal-aparati
        :show.sync="show"
        ref="staticModalAparati"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{'Izbor analizatora: '}}
          <span
            style="color: #e34a4a;"
          >{{this.analiza.ime + ' (' + this.analiza.analit + ')'}}</span>
        </div>
        <div>
          <vuestic-accordion-obrada>
            <vuestic-collapse-aparati
              v-for="element in analysers"
              :key="element._id"
              :analyser="element"
            >
              <span slot="header">{{element.ime + ', ' + element.site.naziv}}</span>
              <div slot="body">
                <div class="va-row"></div>
              </div>
            </vuestic-collapse-aparati>
          </vuestic-accordion-obrada>
        </div>
      </vuestic-modal-aparati>

      <!--  -->
      <vuestic-modal-results-multi
        :show.sync="show"
        ref="staticModalResultsMulti"
        cancelText="ODUSTANI"
        okText="POTVRDI"
      >
        <div slot="title">
          {{'Izbor rezultata: '}}
          <span style="color: #e34a4a;">{{multi_test}}</span>
        </div>
        <div></div>
      </vuestic-modal-results-multi>

      <!-- Patient evaluation, Single -->
      <patient-evaluation-single
        :show.sync="show"
        ref="staticModalEvaluationSingle"
        cancelText="ZATVORI"
        okText="POTVRDI"
      >
        <div slot="title">
