    bus.$on("BakterijeSelect", () => {

      // console.log("Scenario: " + this.CaseScenario);
      // console.log("POTVRDI -> Save selection.");   

      switch (this.CaseScenario) {
        case "MAIBNoR": 
          // Ima definisan skup bakterija - Nema Rezultat 
          this.rezultati[0].rezultat_m = [
            {
              bakterija: false,
              negativan: this.largeModalneg,
              pozitivan: this.largeModalpos,
              _id: null,
              naziv: null,
              opis: null,
              antibiogram: {
                _id: null,
                naziv: null,
                opis: null,
                antibiotici: [
                  {
                    _id: null,
                    rbr_a: null,
                    naziv: null,
                    opis: null,
                    rezultat: null
                  }
                ]
              }
            }
          ];     
                
          if (this.largeModalneg) {  

            this.rezultati[0].rezultat = this.izborNegativnogRezultata;
            this.MicrobiologySaveResults(this.rezultati, "MAIBNeR")

          }else{

            this.izborPozitivnogRezultata = "";
            this.selectedBacteries.forEach(element => {  

              this.izborPozitivnogRezultata = this.izborPozitivnogRezultata + element.opis + "\n"              
              var Antibiotici = []
              var antibiotik = {}

              this.antibiotici.forEach(antibiotik => {
                element.antibiogram.antibiotici.forEach(ant => {
                  if(antibiotik._id == ant){                    
                    antibiotik = {
                      _id: antibiotik._id,
                      rbr_a: antibiotik.rbr_a,
                      naziv: antibiotik.naziv,
                      opis: antibiotik.opis,
                      disabled: false,
                      rezultat: "" // Senzitivan, Intermedijaran, Rezistentan
                    }
                    Antibiotici.push(antibiotik)
                    antibiotik = {}
                  }                  
                });
              });
              
              this.rezultati[0].rezultat_m.push(
                {
                  bakterija: true,
                  negativan: this.largeModalneg,
                  pozitivan: this.largeModalpos,
                  _id: element._id,
                  naziv: element.naziv,
                  opis: element.opis,
                  antibiogram: {
                    _id: element.antibiogram._id,
                    naziv: element.antibiogram.naziv,
                    opis: element.antibiogram.opis,
                    antibiotici: [...Antibiotici]
                  }
                }
              );
              
            });

            this.rezultati[0].rezultat = this.izborPozitivnogRezultata.trim();
            this.MicrobiologySaveResults(this.rezultati, "MAIBPoR")            
          }
          
          break;

        case "MANBNoR": 
          // Nema definisan skup bakterija - Nema Rezultat
          this.rezultati[0].rezultat_m = [
            {
              bakterija: false,
              negativan: this.largeModalneg,
              pozitivan: this.largeModalpos,
              _id: null,
              naziv: null,
              opis: null,
              antibiogram: {
                _id: null,
                naziv: null,
                opis: null,
                antibiotici: [
                  {
                    _id: null,
                    rbr_a: null,
                    naziv: null,
                    opis: null,
                    rezultat: null
                  }
                ]
              }
            }
          ];  

          if (this.largeModalneg) {

            this.rezultati[0].rezultat = this.izborNegativnogRezultata;
            this.MicrobiologySaveResults(this.rezultati, "MANBNeR")

          }else{

            this.rezultati[0].rezultat = this.izborPozitivnogRezultata;
            this.MicrobiologySaveResults(this.rezultati, "MANBPoR")

          }
          
          break;
      
        default:
          // console.warn("This feature has been disabled.");
          break;
      }
    });

    bus.$on("BakterijeCancel", () => {

      // console.log("Scenario: " + this.CaseScenario);
      // console.log("ODUSTANI -> Cancel selection."); 

      this.RezultatMikrobioloski.Bakterije.forEach(element => {
        element.chosen = false            
      });

      this.RezultatMikrobioloski.OstaleBakterije.forEach(element => {
        element.chosen = false            
      });

      this.selectedBacteries = []; 
      bus.$emit('CloseBakterijeModal') 
    });

    bus.$on("BakterijeReset", () => {
      // console.log('class="glyphicon glyphicon-erase"');
      // class="glyphicon glyphicon-erase"      
      
      switch (this.CaseScenario) {
        case "MAIBNoR":          
          this.largeModalneg = false;
          this.largeModalpos = false;
          this.Header = "Predefinisani rezultati";
          this.izborNegativnogRezultata = "";
          this.izborPozitivnogRezultata = "";      
          this.sveBakterije = false;         

          this.RezultatMikrobioloski.Bakterije.forEach(element => {
            element.chosen = false            
          });

          this.RezultatMikrobioloski.OstaleBakterije.forEach(element => {
            element.chosen = false            
          });

          this.selectedBacteries = [];
          
          break;

        case "MANBNoR": // Nema definisan skup bakterija - Nema Rezultat
          this.largeModalneg = false;
          this.largeModalpos = false;
          this.izborNegativnogRezultata = "";
          this.izborPozitivnogRezultata = "";      
          this.sveBakterije = false;
          
          break;
      
        default:
          console.warn("This feature has been disabled.");
          break;
      }
      
    });

    bus.$on("refresh", () => {
      this.$refs.staticModalDeleteResult.open();      
