      this.izborNegativnogRezultata = "";
      this.izborPozitivnogRezultata = "";  
      this.rezultati[0].rezultat =  "";
      this.rezultati[0].rezultat_m = []
      this.sveBakterije = false;
      // console.log("Delete all results.");
      this.MicrobiologySaveResults(this.rezultati, "Unknown")
    });

    bus.$on("tabChanged", data => {   
      this.micro_tab = data
      // console.log("Active tab: " + this.micro_tab)
      this.isLoading = true
      // Need to be debugged.
      document.getElementById(this.$store.state.sid).click();
      setTimeout(() => {
        document.getElementById(this.$store.state.sid).click();
        this.isLoading = false
      }, 500);
    });
  
    bus.$on("MicroClick", data => { 
      if(this.MikrobioloskiUzorak){
        if(data){
          console.log("Close sample.")
        }else{
          console.log("Open sample.")
        } 
      }       
    });

    bus.$on("Antibiotik", data => { 

      if (this.sample.status != "ODOBREN" && this.sample.status != "VERIFICIRAN") {
        this.antibioticiTMP = [];
      
        this.antibiotici.forEach(element => {
          element.chosen = false
          element.disabled = false                              
        });

        this.selectedBacteries.forEach(selected => {
          if(selected.opis == data ){          
            selected.antibiogram.antibiotici.forEach(ant => {
              this.antibiotici.forEach(antibiotik => {               
                if(antibiotik._id == ant._id){
                  antibiotik.disabled = true
                  antibiotik.chosen = true
                  antibiotik.rezultat = ant.rezultat                
                }                          
              });                        
            });
          }           
        });
        this.$refs.largeModalAntibiotici.open();
      }
      
    });

    bus.$on("antibioticiSelect", () => { 

      if(this.antibioticiTMP.length){

        this.antibioticiTMP.forEach(element => {
          this.rezultati[0].rezultat_m.forEach(selected => {
            if(selected.opis == this.micro_tab ){           
              selected.antibiogram.antibiotici.push(element)
            }           
          });              
        });
            
        this.MicrobiologySaveResults(this.rezultati, "MAIBPoR")

      }else{
        // console.warn("Nothing changed.")
        bus.$emit('CloseBakterijeModal') 
      }

    });
