    LabAssayEval(e, test) {
      this.analiza_eval = test;
      e.preventDefault();
      this.spinner = false;
      this.showLineChart = false;
      let palette = store.getters.palette;
      this.table_header = test.ime + " | " + test.analit;
      if (!test.multidata) {
        this.isLoading = true;
        this.Procjena = [];

        http
          .get(
            "/labassay/evaluation/single" +
              "?token=" +
              this.$store.state.token +
              "&site=" +
              this.$store.state.site +
              "&range=" +
              "14",
            {}
          )
          .then(response => {
            if (response.data.Results.length) {
              var obj = {};
              var count = 0;

              response.data.Results.forEach(element => {
                count++;
                element.rezultati.forEach(rez => {
                  if (
                    rez.labassay === test.labassay_id &&
                    element.verificiran &&
                    rez.rezultat[rez.rezultat.length - 1].rezultat_f.trim() !=
                      ""
                  ) {
                    obj.rezultat =
                      rez.rezultat[rez.rezultat.length - 1].rezultat_f;
                    obj.jedinica =
                      rez.rezultat[rez.rezultat.length - 1].jedinice_f;
                    obj.analit = "";
                    obj.interp = rez.interp;
                    obj.extend = rez.extend;
                    obj.refd = rez.refd;
                    obj.refg = rez.refg;
                    obj.sid = element.id;
                    obj.updated_at =
                      rez.rezultat[rez.rezultat.length - 1].updated_at;

                    var datum =
                      JSON.stringify(
                        rez.rezultat[rez.rezultat.length - 1].updated_at
                      ).slice(9, 11) +
                      "." +
                      JSON.stringify(
                        rez.rezultat[rez.rezultat.length - 1].updated_at
                      ).slice(6, 8) +
                      "." +
                      JSON.stringify(
                        rez.rezultat[rez.rezultat.length - 1].updated_at
                      ).slice(1, 5);
                    var time = JSON.stringify(
                      new Date(
                        new Date(
                          rez.rezultat[rez.rezultat.length - 1].updated_at
                        ).getTime() -
                          new Date(
                            rez.rezultat[rez.rezultat.length - 1].updated_at
                          ).getTimezoneOffset() *
                            60000
                      ).toISOString()
                    ).substring(12, 17);

                    obj.datum = datum;
                    obj.vrijeme = time;
                    obj.kontrola = this.Interpretacija(
                      "",
                      obj.rezultat,
                      obj.jedinica,
                      obj.refd,
                      obj.refg,
                      this.$store.state.site,
                      obj.interp,
                      obj.extend,
                      this.spec
                    )[5];

                    this.Procjena.push(obj);
                    obj = {};
                  }
                });

