              var bakterijei = [];
              var neg_rezultati = [];
              var poz_rezultati = [];
              this.uzorak_micro = this.rezultati[0].analit

              neg_rezultati = [...this.rezultati[0].neg_description]
              poz_rezultati = [...this.rezultati[0].pos_description]

              if (this.rezultati[0].bakterije.length) {

                // console.log("Ima bakterije.");
                bakterioloski = true;

                var counter = 0;
                var ima = false;

                this.bakterije.forEach(element => {
                  element.chosen = false;
                  counter = 0;
                  ima = false;
                  this.rezultati[0].bakterije.forEach(bakt => {
                    counter++;
                    if (element._id == bakt) {
                      ima = true;
                    }

                    if (counter === this.rezultati[0].bakterije.length && !ima) {
                      bakterije.push(element);
                    }

                    if (counter === this.rezultati[0].bakterije.length && ima) {
                      bakterijei.push(element);
                    }
                  });
                });
               
                switch (this.rezultati[0].rezultat_m.length) {
                  case 0:
                    // Ima definisan skup bakterija - Nema Rezultat
                    this.CaseScenario = "MAIBNoR"; 
                    this.selectedBacteries = [];
                    break;
                  case 1:
                    // Ima definisan skup bakterija - Negativan Rezultat  
                    this.CaseScenario = "MAIBNeR"; 
                    this.selectedBacteries = [];

                    negativan = true;

                    if(!neg_rezultati.includes(this.rezultati[0].rezultat.trim())){
                      neg_rezultati.unshift(this.rezultati[0].rezultat.trim())
                    }
                                      
                    break;  

                  default:
                    // Ima definisan skup bakterija - Pozitivan Rezultat
                    this.CaseScenario = "MAIBPoR";
                    this.selectedBacteries = [];
                    this.bakterijeTabs = [];

                    this.rezultati[0].rezultat_m.forEach(element => {
                      if(element.bakterija){
                        this.selectedBacteries.push(element)
                        this.bakterijeTabs.push(element.opis)
                      }                      
                    });
                    
                    // console.log("Izabrane bakterije:")
                    // console.log(this.selectedBacteries)
                    
                    let maxVal = this.selectedBacteries.reduce((max, val) => max.antibiogram.antibiotici.length > val.antibiogram.antibiotici.length ? max : val);

                    if(this.micro_tab == ""){
                      this.micro_tab = maxVal.opis
                    }
                    
                    console.log("Active tab: " + this.micro_tab)

                    bakterije.forEach(element => {
                      this.selectedBacteries.forEach(bact => {
                        if(element._id == bact._id){
                          element.chosen = true
                        }
                        
                      });                      
                    });

                    bakterijei.forEach(element => {
                      this.selectedBacteries.forEach(bact => {
                        if(element._id == bact._id){
                          element.chosen = true
                        }
                        
                      });                      
                    });
                    
                    pozitivan = true; 
                    if(!poz_rezultati.includes(this.rezultati[0].rezultat.trim())){
                      poz_rezultati.unshift(this.rezultati[0].rezultat.trim())
                    }
                    
                    break;
                }

              } else {

                // console.log("Nema bakterije.");                
                bakterioloski = false;

                switch (this.rezultati[0].rezultat_m.length) {

                  case 0:
                    // Nema definisan skup bakterija - Nema Rezultat 
                    this.CaseScenario = "MANBNoR"; 
                    neg_rezultati = this.rezultati[0].neg_description
                    poz_rezultati = this.rezultati[0].pos_description
                                       
                    break;

                  case 1:
                     // Nema definisan skup bakterija - Negativan Rezultat   
                    if(this.rezultati[0].rezultat_m[0].negativan === true){
                      this.CaseScenario = "MANBNeR"; 

                      negativan = true;

                      if(!neg_rezultati.includes(this.rezultati[0].rezultat.trim())){
                        neg_rezultati.unshift(this.rezultati[0].rezultat.trim())
                      }                      
                     
                    }else{
                      // Nema definisan skup bakterija - Pozitivan Rezultat
                      this.CaseScenario = "MANBPoR"; 

                      pozitivan = true; 

                      if(!poz_rezultati.includes(this.rezultati[0].rezultat.trim())){
                        poz_rezultati.unshift(this.rezultati[0].rezultat.trim())
                      }
                                            
                    }    

                    break;  

                  default:                    
                    // Nema definisan skup bakterija - Negativan Rezultat  
                    this.CaseScenario = "MANBNeR";

                    break;
                }

              }   

              this.RezultatMikrobioloski = {
                negativan: negativan,
                pozitivan: pozitivan,
                Bakterije: [...bakterijei],
                OstaleBakterije: [...bakterije],
                rezultat_f: this.rezultati[0].rezultat,
                rezultat_m: [...this.rezultati[0].rezultat_m],
                neg_descriptions: [...neg_rezultati],
                pos_descriptions: [...poz_rezultati],
                bakterioloski: bakterioloski
              };

            } else {
              // console.log("Uzorak nije mikrobiološki.");
              this.CaseScenario = "Unknown";
              this.RezultatMikrobioloski = {
                negativan: null,
                pozitivan: null,
                Bakterije: null,
                OstaleBakterije: null,
                rezultat_f: "",
                rezultat_m: [],
                neg_descriptions: [],
                pos_descriptions: [],
                bakterioloski: false
              };
            }
