        .post("/rezultati/odobravanje/calculate/" + this.$store.state.sid, {
          _id: id,
          token: this.$store.state.token,
          site: this.$store.state.site,
          ime: ime,
          pid: this.pid
        })
        .then(res => {
          http
            .get(
              "rezultati/odobravanje/samples/" +
                this.$route.params.patient +
