            )
            .then(res => {
              var test = {};

              if (res.data.data.length) {
                res.data.data.sort(function(a, b) {
                  return a.testovi.labassay.grupa +
                    a.testovi.labassay.grouporder ==
                    b.testovi.labassay.grupa + b.testovi.labassay.grouporder
                    ? 0
                    : +(
                        a.testovi.labassay.grupa +
                          a.testovi.labassay.grouporder >
                        b.testovi.labassay.grupa + b.testovi.labassay.grouporder
                      ) || -1;
                });

